.app__skills {
    flex: 1;
    width: 100%;
    flex-direction: column;
}

.head-tit {
    background-image: linear-gradient(90deg, #313bac, #6b7688);
    background-clip: text;
    color: transparent;
}

.app__skills-container {
    width: 80%;
    margin-top: 3rem;
    display: flex;
    flex-direction: column;

    @media screen and  (max-width : 900px) {
        width: 100%;
        flex-direction: column;
    }
}

.app__skills-list {
    flex: 1;
    display: flex;
    flex-wrap: wrap;
    justify-content: flex-end;
    align-items: flex-end;
    margin-right: 5rem;

    @media screen and (max-width : 900px) {
        margin-right: 0;
        justify-content: center;
        align-items: center;
    }
}

.app__skills-item {
    flex-direction: column;
    text-align: center;
    margin: 1rem;
    transition: all 0.3s ease-in-out;
    
    div {
        width: 90px;
        height: 90px;
        border-radius: 50%;
        background-color: #fff;

        img {
            width: 50%;
            height: 50%;
        }

        &:hover {
            box-shadow: 0 0 25px #fef4f5;
        }

        @media screen and (min-width : 2000px) {
            width: 150px;
            height: 150px;
        }

        @media screen and (max-width : 300px) {
            width: 70px;
            height: 70px;
        }
    }

    p {
        font-weight: 500;
        margin-top: 0.5rem;
    }

    @media screen and (min-width : 2000px) {
        margin: 1rem 2rem;

        p {
            margin-top: 1rem;
        }
    }

    @media screen and (max-width : 900px) {
        margin: 1rem 1.8rem;
    }
}

.app__skills-exp-work {
    width: 100%;
    display: flex;
    justify-content: flex-start;
    align-items: flex-start;

    margin-bottom: 2rem;

    img {
        width: 75%;
    }

    @media screen and (max-width : 450px) {
        justify-content: center;
        align-items: center;

        img {
            width: 85%;
        }
    }
}